@import "style/mixins.module.scss";

.title {
    margin-bottom: 16px;

    @include respond-to("laptop") {
        margin-bottom: 32px;
        width: 800px;
        margin-right: auto;
        margin-left: auto;
        text-align: center;
    }
}

.benefits-flex-wrapper {
    display: flex;
    width: 800px;
    margin: auto;

    .benefits-group {
        flex: 1;

        .benefits-list {
            .benefit-item {
                font-size: 22px;
                line-height: 32px;
            }
        }
    }

    @include respond-to("mobile") {
        flex-direction: column;
        height: auto;
        width: 100%;

        .benefits-group {
            .benefits-list {
                margin: 0;
                .benefit-item {
                    font-size: 17px;
                    line-height: 24px;
                }
            }
        }
    }
}
